import React, { Component } from 'react';
import { Badge, Card, CardBody, CardHeader, Col, Pagination, PaginationItem, PaginationLink, Row, Table } from 'reactstrap';
import { Button, Input, InputGroup, InputGroupAddon, InputGroupText, Alert } from 'reactstrap';
import { CardGroup, Container, Form, FormGroup, Fade, Collapse, Label, ListGroup, ListGroupItem, ListGroupItemHeading, ListGroupItemText } from 'reactstrap'
import { Modal, ModalBody, ModalFooter, ModalHeader, Popover, PopoverBody, PopoverHeader } from 'reactstrap'
import { getFileHost, getShareHost, getCurrentHost } from '../js/gegeUtil.js'
import { loadMinerResource } from '../../eshop/eshop'
const ZkerPlayer = require('zker-player')
const Resumable = require('resumablejs')
const ShareManager = require('../js/share').default
const { postSafe, loadLocalAccount, signString } = require('../js/gegeUtil')
//
const RTOKEN_UPLOAD_SIZE = 1;
const rTokenKey = 'rtoken-key'


class WebMiner extends Component {

  constructor(props) {
    super(props);
    let res = { tags: '', price: '', fee: '', deadline: '', owner: '', id: '' }
    this.state = { collapse: true, resourceList: [], resource: res, rsId: '账号未登陆' }
    this.state.shareMsg = '';
    this.share = new ShareManager();
    this.userSec = ''
    this.fee = 0.01;
    this.deadline = 5;
    this.price = 0;
    this.tags = ''
    this.resName = ''
    this.resId = ''
    this.infoInterval = null
    this.torrentList = []
    this.resume = null;
    this.html5File = null;
    this.torrent = null;
    this.poster = null;
    this.tokenCount = new Map();
  }

  cancelShare() {
    this.setState({ collapse: !this.state.collapse });
  }

  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  componentDidMount() {
    var account = loadLocalAccount();
    if (account && account.secret) {
      this.userSec = account.secret;
      this.setState({ rsId: account.rsId })
    }
    this.share.init(this.onTorrentDone.bind(this), this.onTorrentStart.bind(this))
  }

  initResumable(infoHash, signature) {
    let host = getFileHost()
    let target = host + '/share/' + infoHash + '/' + signature;
    //console.log('target=' + target)
    this.resume = new Resumable({
      target,
      chunkSize: 1 * 1024 * 1024,
      simultaneousUploads: 4,
      testChunks: false,
      throttleProgressCallbacks: 1,
      method: "octet"
    });
    //
    this.resume.on('fileAdded', ((file) => {
      console.log('resume got file ' + file)
      if (this.resume)
        this.resume.upload()
      else {
        console.log('this.resume is null')
      }
    }).bind(this))
    this.resume.on('pause', function () {
      console.log('resume pause')
    });
    this.resume.on('complete', () => {
      console.log('resume complete')
      this.setState({ shareMsg: '文件上传成功' })
    });
    this.resume.on('fileSuccess', (file, message) => {
      console.log('resume fileSuccess')
    });
    this.resume.on('fileError', (file, message) => {
      console.log('resume fileError')
      this.setState({ shareMsg: '发布资源出错了', uploadAgain: true })
    });
    this.resume.on('fileProgress', (file) => {
      let p = file.progress() * 100;
      this.setState({ shareMsg: '文件上传中 ' + p + '%' })
    });
    this.resume.on('cancel', function () {
      console.log('resume cancel')
    });
    this.resume.on('uploadStart', function () {
      console.log('resume uploadStart')
    });
  }

  prettyBytes(num) {
    var exponent, unit, neg = num < 0, units = ['B', 'kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB']
    if (neg) num = -num
    if (num < 1) return (neg ? '-' : '') + num + ' B'
    exponent = Math.min(Math.floor(Math.log(num) / Math.log(1000)), units.length - 1)
    num = Number((num / Math.pow(1000, exponent)).toFixed(2))
    unit = units[exponent]
    return (neg ? '-' : '') + num + ' ' + unit
  }

  updateTorrentStatus() {
    //console.log('size ' + this.torrentList.length)
    this.torrentList.map((torrent) => {
      //uploadSpeed peers
      let peersSpan = document.querySelector('#pid' + torrent.infoHash);
      if (peersSpan)
        peersSpan.innerHTML = torrent.numPeers;
      //
      let speedSpan = document.querySelector('#sid' + torrent.infoHash);
      if (speedSpan)
        speedSpan.innerHTML = this.prettyBytes(torrent.uploadSpeed) + '/s';
    })
  }


  onTorrentStart(html5Files) {
    this.setState({ shareMsg: '处理中...(大文件可能会比较慢,请耐心等待)' })
    if (html5Files) {
      this.html5File = html5Files[0];
    }
  }

  addServerResoruce() {
    let downloadId = this.state.downloadId;
    if (downloadId) {
      this.download(downloadId)
    } else {
      //get one random id from server
      loadMinerResource((ok, resources) => {
        if (ok)
          resources.map((r) => {
            this.download(r.id)
          })
      })
    }
  }

  handleResourceToken(resId, rtoken) {
    let lastToken = localStorage.getItem(lastKey)
    const lastKey = resId + '-lst';
    if (rtoken === lastToken) {
      //exist
    } else {
      localStorage.setItem(lastKey, rtoken)
      //save new token
      let rtokens = localStorage.getItem(rTokenKey);
      if (!rtokens) {
        rtokens = ''
      }
      rtokens = rtoken + ';' + rtokens
      localStorage.setItem(rTokenKey, rtokens)
    }
    //update count
    if (this.tokenCount.has(resId)) {
      let count = this.tokenCount.get(resId)
      this.tokenCount.set(resId, count + 1)
    } else {
      this.tokenCount.set(resId, 1)
    }
    //
    let count = this.tokenCount.get(resId)
    let tokenSpan = document.querySelector('#rto' + resId);
    if (tokenSpan)
      tokenSpan.innerHTML = count + '';
    if (this.tokenCount.size > 0 && this.tokenCount.size % RTOKEN_UPLOAD_SIZE == 0) {
      this.uploadRtokens()
    }
  }

  uploadRtokens() {
    var account = loadLocalAccount();
    let rtokens = localStorage.getItem(rTokenKey);
    let data = { action: 'rtoken', type: 'reimburse', tokens: rtokens.split(';'), minerPk: account.pk }
    let url = getShareHost() + "/bshop"
    postSafe(url, data, (ok, res) => {
      if (ok) {
        //clear exist record
        localStorage.setItem(rTokenKey, '')
      }
    })
  }

  download(downloadId) {
    var config = { secret: this.userSec };
    var player = new ZkerPlayer(downloadId, config)
    let resourceTokenCallBack = (rtoken) => {
      this.handleResourceToken(downloadId, rtoken)
    }
    player.download(downloadId, { resourceTokenCallBack }, (torrent) => {
      let resId = torrent.infoHash
      let resName = torrent.name;
      var rlist = this.state.resourceList;
      rlist.push({ id: resId, name: resName })
      this.setState({ resourceList: rlist })
      this.torrentList.unshift(torrent)
    })
  }

  onTorrentDone(torrent) {
    this.setState({ ready: true })
    if (torrent) {
      this.torrent = torrent;
      var info = 'Torrent info hash: ' + torrent.infoHash +
        'magnetURI=' + torrent.magnetURI +
        ' torrentFileBlobURL="' + torrent.torrentFileBlobURL + 'name=' + torrent.name;
      //console.log(info);
      this.resId = torrent.infoHash
      this.resName = torrent.name;
      var rlist = this.state.resourceList;
      rlist.push({ id: this.resId, name: this.resName })
      this.setState({ resourceList: rlist })
      torrent.key = torrent.infoHash;
      //tags is not set yet
      //torrent.tags = this.tags;
      this.torrentList.unshift(torrent)
      // torrentPoster(torrent, (err, buf, extension) => {
      //   if (err) {
      //     console.log(err)
      //   } else {
      //     // console.log('torrentPoster ' + err + ' ' + buf.toString('base64') + ' ext=' + extension)
      //     this.poster = buf.toString('base64')
      //   }
      // })
    }
    if (!this.infoInterval) {
      this.infoInterval = setInterval(this.updateTorrentStatus.bind(this), 3000);
    }
    this.setState({ shareMsg: '' })
  }

  updateDownloadId(e) {
    if (e && e.target) {
      let downloadId = e.target.value;
      this.setState({ downloadId })
    }
  }


  updateFee(e) {
    if (e && e.target) {
      this.fee = e.target.value;
    }
  }

  updatePrice(e) {
    if (e && e.target) {
      this.price = e.target.value;
    }
  }

  updateTags(e) {
    if (e && e.target) {
      this.tags = e.target.value;
    }
  }

  updateDeadline(e) {
    if (e && e.target) {
      this.deadline = e.target.value;
    }
  }

  renderAddFile() {
    return (<Row>
      <Col xs="12">
        <Fade>
          <Card>
            <CardHeader>
              <i className="icon-share" onClick={this.toggle.bind(this)}>流量矿工赚积分</i>
              <div className="card-header-actions">
                <Button color="link" className="card-header-action btn-setting"><i className="icon-settings"></i></Button>
                <Button color="link" className="card-header-action btn-minimize" data-target="#collapseExample" onClick={this.toggle.bind(this)}><i className="icon-arrow-up"></i></Button>
                <Button color="link" className="card-header-action btn-close" onClick={this.toggleFade}><i className="icon-close"></i></Button>
              </div>
            </CardHeader>
            <Collapse isOpen={this.state.collapse} id="collapseExample">
              <CardBody>
                {/* <FormGroup>
                  <Label htmlFor="appendedInputButtons">账户密码:</Label>
                  <div className="controls">
                    <InputGroup>
                      <Input id="appendedInputButtons" size="16" type="text" value={this.secret} onChange={this.updateSecret.bind(this)} placeholder='输入账户密码共享资源' />
                    </InputGroup>
                  </div>
                </FormGroup> */}
                <FormGroup>
                  <div className="controls">
                    <InputGroup className="input-prepend">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>我的账号:</InputGroupText>
                      </InputGroupAddon>
                      <Input id="prependedInput" size="16" type="text" value={this.state.rsId} />
                    </InputGroup>
                  </div>
                </FormGroup>
                <FormGroup>
                  <div className="controls">
                    <InputGroup className="input-prepend">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>获取服务器资源:</InputGroupText>
                      </InputGroupAddon>
                      <Input id="appendedInputButtons" size="16" type="text" onChange={this.updateDownloadId} placeholder='此处填写资源ID,ID为空则从服务器随机下载' />
                      <Button color="danger" onClick={() => this.addServerResoruce()}>下载</Button>
                    </InputGroup>
                  </div>
                </FormGroup>
                <FormGroup>
                  <div className="controls">
                    <InputGroup className="input-prepend">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>选择本地资源提供流量:</InputGroupText>
                      </InputGroupAddon>
                      <input name="upload" type="file"></input>
                    </InputGroup>
                  </div>
                </FormGroup>
              </CardBody>
            </Collapse>
          </Card>
        </Fade>
      </Col>
    </Row>)
  }

  renderUploadAgainButton() {
    if (!this.state.uploadAgain)
      return null
    return (
      <Button type="submit" color="primary" onClick={(e) => this.uploadAgain()}>重新上传</Button>
    )
  }



  renderResourceItems() {
    let items = this.state.resourceList.map((res) => {
      let pid = 'pid' + res.id;
      let sid = 'sid' + res.id;
      let rid = 'rto' + res.id;
      let confirms = 0;
      return (<ListGroupItem action>
        <ListGroupItemHeading>{res.name}   <PopoverItem id={res.id} res={res} /></ListGroupItemHeading>
        <ListGroupItemText>
          <code> <span id={pid}></span>节点</code>. &#x2197;<code><span id={sid}></span>上传速度</code> <code><span id={rid}>{confirms}</span>个确认信息</code>
        </ListGroupItemText>
      </ListGroupItem>)
    })
    return items;
  }

  renderShareList() {
    return (<Row>
      <Col sm="12" xl="6">
        <Card>
          <CardHeader>
            <i className="fa fa-align-justify"></i><strong>资源列表</strong>
            <Alert color="danger"><small>流量矿工通过分享本机硬盘与网络流量为其他用户提供资源下载服务。其他用户成功下载资源后会留下确认信息，流量矿工用收集到的确认信息换取眞客积分。请不要刷新或关闭此页面，保持此页面打开状态下，网络上其他用户才可以下载资源</small></Alert>
          </CardHeader>
          <CardBody>
            <ListGroup>
              {this.renderResourceItems()}
            </ListGroup>
          </CardBody>
        </Card>
      </Col>
    </Row>)
  }

  render() {
    return (
      <div className="animated fadeIn">
        {this.renderAddFile()}
        <div>
          <Alert color="info">{this.state.shareMsg}</Alert>
        </div>
        {this.renderShareList()}
      </div>

    );
  }
}


class PopoverItem extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      popoverOpen: false
    };
  }

  toggle() {
    this.setState({
      popoverOpen: !this.state.popoverOpen,
    });
  }

  render() {
    return (
      <span>
        <span className='alert-primary  fade show' id={'Popover-' + this.props.id} onClick={this.toggle}>
          (分享链接)
          </span>
        <Popover placement='top' isOpen={this.state.popoverOpen} target={'Popover-' + this.props.id} toggle={this.toggle}>
          <PopoverHeader>分享这个资源的链接,获取更多确认信息</PopoverHeader>
          <PopoverBody>{getCurrentHost() + "/#/btb#" + this.props.res.id + '#' + this.props.res.name}</PopoverBody>
        </Popover>
      </span>
    );
  }
}

export default WebMiner;
