const ZkerPlayer = require('zker-player')

export default class ShareManager {

    constructor() {
        this.player = new ZkerPlayer('', {})
        this.torrentCallback = null
        this.onStartCallback = null
    }

    init(torrentCallback, onStartCallback, uploadInput) {
        this.torrentCallback = torrentCallback;
        this.onStartCallback = onStartCallback;
        var up = uploadInput;
        if (!up)
            up = document.querySelector('input[name=upload]')
        if (up) {
            this.uploadElement(up, this.onStartCallback, (function (err, files) {
                if (err) return err;
                files = files.map(function (file) { return file.file })
                this.onFiles(files)
            }).bind(this))
        }
    }

    isTorrentFile(file) {
        //var extname = path.extname(file.name).toLowerCase()
        return file.name.indexOf('.torrent') !== -1;
    }

    isNotTorrentFile(file) {
        return !this.isTorrentFile(file)
    }

    onSeedTorrent(torrent) {
        torrent.on('warning', function (warn) {
            console.log(warn);
        })
        torrent.on('error', function (err) {
            console.log(err);
        })
        this.torrentCallback(torrent);
    }

    seedContent(content, torrentCallback) {
        let buf = Buffer.from(content)
        let tor = this.player.seed(buf, torrentCallback);
        tor.on('infoHash', (infoHash) => {
            torrentCallback(tor);
        })
    }

    seed(files) {
        if (files.length === 0) return
        //console.log('Seeding ' + files.length + ' files')
        this.onStartCallback(files)
        // Seed from WebTorrent
        setTimeout(() => {
            let tor = this.player.seed(files, this.onSeedTorrent.bind(this));
            tor.on('infoHash', (infoHash) => {
                this.torrentCallback(tor);
            })
        }, 1000);
    }
    //
    onFiles(files) {
        console.log('got files:')
        files.forEach(function (file) {
            // console.log(' - %s (%s bytes)', file.name, file.size)
        })

        // .torrent file = start downloading the torrent
        //files.filter(isTorrentFile).forEach(downloadTorrentFile)

        // everything else = seed these files
        this.seed(files)
    }

    uploadElement(elem, onStartCallback, opts, cb) {
        if (typeof opts === 'function') {
            cb = opts;
            opts = {};
        }
        if (typeof opts === 'string') opts = { type: opts };

        elem.addEventListener('change', function (ev) {
            if (elem.files.length === 0) return cb(null, []);
            if (onStartCallback)
                onStartCallback(null)
            var reader = new FileReader();
            var index = 0;
            var results = [];

            reader.addEventListener('load', function (e) {
                results.push({
                    file: elem.files[index],
                    target: e.target
                });
                index++;
                if (index === elem.files.length) cb(null, results)
                else read(index)
            });
            read(index);

            function read(index) {
                var file = elem.files[index];
                if (opts.type === 'text') {
                    reader.readAsText(file);
                }
                else if (opts.type === 'url') {
                    reader.readAsDataURL(file);
                }
                else reader.readAsArrayBuffer(file);
            }
        });
    }

}
